@import 'styles/variaveis';
@import 'styles/breakpoints';

.subscription{
    background-color: $cor2;
    height: auto;
    max-height: 100%;
    position: fixed;
    padding: 0.5rem;
    box-shadow: $bxshadow;
    margin-right: 10px;
    margin-top: 70px;
   
    
    img{
        object-fit: contain;
    }
    
    @media (max-width: $mobile){
        display: none;
    }
 
}

